import common from '../../common/common.js'
	
export default {
	data() {
		return {
			baseUrl:common.baseUrl,		
			userInfo:[],
			shopid:0,				//店铺ID
			shop:[],				//店铺信息
			qisong_cha:0,			//起送价
			keys:'',				//关键字
			
			goodsList:[],			//商品列表
			canbuy:0,
			isall:0,				//是否全部
			page:0,
			
			mycartList:[],			//购物车
			totalnums:0,			//总数量
			totalamount:0.00,		//总金额
			opened:false,			//是否弹出购物车信息
			
			goods:[],				//当前选择商品
			hideitem:1,
			guige:[],
			guige_name:'',       //选择的规格名称
			guige_key: -1,     //选中的规格键值
			guige_price:0,         //选中规格的价格
			guige_stocks:0,
			nums:1,
			
			showForm:false,
		};
	},
	onLoad(option) {
		var that = this
		
		var shopid = option.id
		that.shopid = shopid
		var userInfo = uni.getStorageSync('userInfo');
		if(userInfo != ''){
			that.userInfo = userInfo
		}
		uni.setStorageSync('shopid', shopid);
		
		var mycartList = uni.getStorageSync('mycartList');
		var timestamp = Date.parse(new Date());
		var mycartList_expire = uni.getStorageSync('mycartList_expire');
		if( timestamp > mycartList_expire && mycartList){
			//过期
			uni.setStorageSync('mycartList', '');
			uni.setStorageSync('mycartList_expire', '');
		}
		that.mycartList = mycartList != '' ? mycartList : []
		
		//获取店铺信息
		that.getShopInfo();	
		
		that.getSearchGoodsList();		
	},
	
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  
	  that.resetAttr();
	  //搜索店铺列表
	  //that.getSearchShopList();
	  
	  console.log('下拉刷新中...');		  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/merchant?tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/merchant'
		}
		return {
		  title: '本地优选生活信息平台-凡迪优品',
		  path: shareUrl,
		}
	},
	onReachBottom() {
		var that = this
		that.loadmore();
	},
	methods: {
		//购物车数量加
		cartNumsADD:function(){
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.guige_stocks
			var guige_key = that.guige_key
			if(guige_key == -1){
				uni.showToast({ title: '请先选择规格',icon: 'loading',duration: 1000 })
				return false
			}
			
			if(limitnums > 0){
			  //有限购
			  if(nums < limitnums){
				if(nums < stocks){
				  //有库存
				  nums++
				  that.nums = nums
				}else{
				  //库存存不足
				  uni.showToast({ title: '库存不足',icon: 'loading',duration: 1000 })
				  return false
				}        
			  }else{
				//超过限购数量
				uni.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				return false
			  }
			}else{
			  if (nums < stocks) {
				//有库存
				nums++
				that.nums = nums
			  } else {
				//库存存不足
				uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				return false
			  }
			  
			}
			//console.log(that.data.nums)
		},
		
		//购物车数量减
		cartNumsJIAN: function () {
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.guige_stocks
		
			if(nums > 1){
			  nums--
			  that.nums =  nums
			}else{
			  return false;
			}
		},
		
		//手动更新数量
		updateCartNums:function(e){
			var that = this
			var nums = parseInt(e.detail.value);
			var limitnums = that.limitnums
			var stocks = that.guige_stocks
			
			if (nums > 0){
			  if (limitnums > 0) {
				//有限购
				if (nums <= limitnums) {
				  if (nums <= stocks) {
					//有库存          
					that.nums =  nums
				  } else {
					//库存存不足
					uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
					that.nums =  stocks
					return false
				  }
				} else {
				  //超过限购数量
				  uni.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				  that.nums =  limitnums
				  return false
				}
			  } else {
				if (nums <= stocks) {
				  //有库存        
				  that.nums =  nums
				} else {
				  //库存存不足
				  uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				  return false
				}
		
			  }
			}else{
			  that.nums =  1
			}
		},
		//选择规格
		  chooseGuige:function(e){
			var that = this
			var key = e.currentTarget.dataset.key
			var guige = e.currentTarget.dataset.guige
			var price = e.currentTarget.dataset.price			
			
			if (key == that.guige_key){
				that.guige_name = ''
				that.guige_key = -1
				that.guige_price = 0
				that.guige_stocks = 0
			}else{
				that.guige_name = guige
				that.guige_key = key
				that.guige_price = price
				that.guige_stocks = that.guige[key][3]
			}
			
		  },
		//关闭弹出层
		  closePopCart:function(){
			var that = this
			that.hideitem = 1;
			that.guige_name = ''
			that.guige_key = -1
			that.guige_price = 0
			that.guige_stocks = 0;
			that.nums = 1;
		  },
		
		  //弹出
		  popCart:function(){
			var that = this
			that.hideitem = 0;
		  },
		  
		  //加入购物车
		  addToCart:function(){
			var that = this
			that.checkAttr();
			if (that.guige_key == -1) {
			  wx.showToast({
				title: '请选择规格',
				icon: 'none',
				duration: 1000
			  })
			  return false
			}
		
		   
		
			var userInfo = uni.getStorageSync("userInfo");
			var id = that.goods.goods_id;
			var title = that.goods.goods_title;
			var nums = that.nums;
			var guige_name = that.guige_name;
			var price = that.guige_price;
		
			
			if (userInfo.users_id > 0) {
			  
			  uni.showToast({
				title: '加载中',
				icon: 'loading',
				duration: 1000
			  })
			  var guige = that.guige
			  var kindex = that.guige_key;
			  var stocks = guige[kindex][3];
			  var mycartList = that.mycartList;
			  var userInfo = uni.getStorageSync('userInfo');
			  
			  
			  if( stocks <= 0 || stocks < nums){
			  	uni.showToast({
			  		title:'库存不足',
			  		duration:1500,
			  		icon:'none'
			  	})
			  	return false
			  }
			  
			  var ishas = 0;
			  console.log("stocks",stocks)
			  for(var i=0;i<mycartList.length;i++){
			  	if(mycartList[i][1] == id && mycartList[i][5] == kindex){
					var tnum = mycartList[i][2]
					var t_nums = tnum +nums 
					console.log("t_nums",t_nums)
			  		if( stocks < t_nums){
			  			uni.showToast({
			  				title:'库存不足',
			  				duration:1500,
			  				icon:'none'
			  			})
			  			return false
			  		}
			  		mycartList[i][2] = mycartList[i][2] + nums;							
			  		ishas = 1;
			  	}
			  }
			  	
			  	console.log("mycartList",mycartList)				
			  if(ishas == 0){
			  	var temparr = [];
			  	temparr.push(that.goods.goods_shopid);		//店铺ID
			  	temparr.push(id);						//商品ID
			  	temparr.push(nums);						//数量
			  	temparr.push(price);					//价格
			  	temparr.push(title);					//标题
			  	temparr.push(kindex);					//规格KEY
			  	temparr.push(guige_name);				//规格名称
			  	console.log(temparr)
			  	mycartList.push(temparr);
			  }
			  console.log(mycartList);
			  
			  that.updateCartInfo();
			  
			  //wx.setStorageSync('mycartList', mycartList);
			  var timestamp = Date.parse(new Date());
			  var expire = timestamp + 3600000
			  uni.setStorageSync('mycartList', mycartList);
			  uni.setStorageSync('mycartList_expire', expire);
			  that.closePopCart();
		
			} else {
			  //app.checkUserLogin();
			  uni.showModal({
				title: '提示',
				showCancel: false,
				content: '亲，您还没有登录，现在去登录',
				success: function (res) { 
				  uni.navigateTo({
					url: '/pages/user/login?tfrom=goods&id='+that.id
				  })
				}
			  })
		
			}
		  },
		
		  //参数验证
		  checkAttr:function(){
			var that = this
		
			if(that.guige_name == ''){
			  uni.showToast({
				title: '请选择规格',
				icon: 'loading',
				duration: 1000
			  })
			  return false
			}else{
			  return true
			}
			
		  
		  },
		//下单,到购物车确认页面
		checkout(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var mycartList = that.mycartList;
			if(userInfo == ''){
				uni.navigateTo({
					url: '/pages/userlogin/userlogin'
				});
				return false;
			}else{				
				setTimeout(function(){
					uni.navigateTo({
						url: '/pages/cart/cart'
					});
				},1000);
				
				
			}
			
		},
		//操作购物车信息
		opcart(e){
			var that = this
			var target = e.currentTarget.dataset
			var id = target.id;
			var index = target.index;
			var op = target.op;
			var price = target.price;
			var title = target.title;
			var stocks = target.stocks;
			var goodsList = that.goodsList;
			var mycartList = that.mycartList;
			var userInfo = uni.getStorageSync('userInfo');			
			var ishas = 0;
			
			var currentTime = new Date().getTime();
			var tnowtime = currentTime/1000;
			var activity = that.activity
			
			
			if (op == 'addOne'){//添加
				if( stocks <= goodsList[index]['nums'] ){
					uni.showToast({
						title:'库存不足',
						duration:1500,
						icon:'none'
					})
					return false
				}
				mycartList[index][2]++;
			}
			if(op == 'subOne'){
				mycartList[index][2]--
				if(mycartList[index][2] == 0){
					mycartList.splice(index,1);
				}
			}
			
			
			that.updateCartInfo();
			
			that.mycartList = mycartList
			if(mycartList == ''){
				that.opened = false
			}
			
			//wx.setStorageSync('mycartList', mycartList);
			var timestamp = Date.parse(new Date());
			var expire = timestamp + 3600000
			uni.setStorageSync('mycartList', mycartList);
			uni.setStorageSync('mycartList_expire', expire);
			
			//that.getGoodsList();
			
		},
		//加入购物车
		operate(e){
			//wx.setStorageSync('mycartList', '');
			var that = this
			var target = e.currentTarget.dataset
			var id = target.id;
			var sindex = target.sindex;
			var index = target.index;
			var op = target.op;
			var price = target.price;
			var title = target.title;
			var stocks = target.stocks;
			var goodsList = that.goodsList;
			var mycartList = that.mycartList;
			var userInfo = uni.getStorageSync('userInfo');
			
			that.goods = goodsList[index]
			that.guige = goodsList[index]['goods_guige']
			
			
			if( stocks <= 0){
				uni.showToast({
					title:'库存不足',
					duration:1500,
					icon:'none'
				})
				return false
			}
			if(goodsList[index]['goods_guige'] != '' || goodsList[index]['goods_guige'] != ''){
				that.popCart();
				return false;
			}
			
			var ishas = 0;
			
			if (op == 'addOne'){//添加
					console.log("goodsList",goodsList);
					console.log("index",index);				
					
					var t_temai = 0;
					for(var i=0;i<mycartList.length;i++){
						if(mycartList[i][1] == id){
							if( stocks <= goodsList[index]['nums']){
								uni.showToast({
									title:'库存不足',
									duration:1500,
									icon:'none'
								})
								return false
							}
							mycartList[i][2]++;							
							ishas = 1;
							goodsList[index]['nums']++;
						}
					}
						
						console.log("mycartList",mycartList)				
					if(ishas == 0){
						var temparr = [];
						temparr.push(that.shop.shop_id);		//店铺ID
						temparr.push(id);						//商品ID
						temparr.push(1);						//数量
						temparr.push(price);					//价格
						temparr.push(title);					//标题
						goodsList[index]['nums'] = 1;
						console.log(temparr)
						mycartList.push(temparr);
					}
					
					
			}
			if(op == 'subOne'){
				for(var i=0;i<mycartList.length;i++){
					if(mycartList[i][1] == id){
						mycartList[i][2]--;
						ishas = 1;
						goodsList[index]['nums']--;
						if(mycartList[i][2] == 0){
							mycartList.splice(i,1);
							goodsList[index]['nums'] = 0;
						}
					}
				}
			}
			console.log(mycartList);
			
			that.goodsList = goodsList;
			that.updateCartInfo();
			
			//wx.setStorageSync('mycartList', mycartList);
			var timestamp = Date.parse(new Date());
			var expire = timestamp + 3600000
			uni.setStorageSync('mycartList', mycartList);
			uni.setStorageSync('mycartList_expire', expire);
			
		},
		
		//更新购物车信息
		updateCartInfo(){
			var that = this
			var totalamount = 0;
			var totalnums = 0;
			var mycartList = that.mycartList;
			//var activity = that.activity
						
			var all_dis = 0;
			for (let j = 0; j < mycartList.length; j++) {
			  
			  totalnums += mycartList[j][2];
			  //var tt = parseFloat(nums_arr[j]) * parseFloat(price_arr[j]);				 
			  totalamount += mycartList[j][2]*mycartList[j][3];
			  var t_amount = mycartList[j][2]*mycartList[j][3];
			  t_amount = t_amount.toFixed(2)
			}		
			totalamount = Math.round(totalamount * 100) / 100;		
		
			that.totalnums = totalnums;
			that.totalamount = totalamount;
			var qisong_cha = that.shop.shop_qisong - totalamount
			that.qisong_cha = qisong_cha.toFixed(2)
			console.log("qisong_cha",qisong_cha)
			
		},
		
		//弹出或隐藏购物车信息
		toggleCartInfo(){
			var that = this
			var opened = that.opened;
			that.opened = opened == true ? false : true;
			
		},
		
		//清空购物车
		clearCart(){
			var that = this
			uni.showModal({
				title: '提示',
				content: '确定要清空吗？',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.removeStorageSync('mycartList');
						uni.removeStorageSync('mycartList_expire');
						that.mycartList = [];
						that.opened = false
						that.peisong = false;
						that.totalamount = 0;
						that.totalnums = 0;
						that.getShopGoodsList();
						console.log('aa');
						that.updateCartInfo();
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		
		//联系商家
		contactShop(){
			var that = this
			var shop_mobile = that.shop.shop_mobile
			uni.makePhoneCall({
				phoneNumber:shop_mobile
			})
		},
		//获取店铺信息
		getShopInfo(){
			var that = this
			var shopid = that.shopid
			var userInfo = uni.getStorageSync('userInfo');
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopInfo',
				method: 'GET',
				data: {
					shopid:shopid,
					userid:userInfo.users_id
				},
				success: res => {
					that.shop = res.data.shop
					var shop = res.data.shop










				},
				fail: () => {},
				complete: () => {}
			});
		},
		//申请查看
		shenqingView(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			console.log("userInfo",userInfo.users_id)
			if(userInfo.users_id  > 0 ){
				that.toggleForm();
				
			}else{
				uni.showModal({
					title: '提示',
					showCancel: false,
					content: '亲，您还没有登录，现在去登录',
					success: function (res) { 
					  uni.navigateTo({
						url: '/pages/userlogin/userlogin?tfrom=products&id='+that.shopid,
						success:function(res){
							console.log(res);
						}
					  })
					}
				})
			}
		},
		
		//提交申请查看表单
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			var title = data.title
			var addr = data.addr
			var contacter = data.contacter
			var mobile = data.mobile
			//检测是否有推荐人
			var userInfo = uni.getStorageSync('userInfo');
			console.log('aaa');
			
			if(title == ''){					
				uni.showToast({
					title: '请输入店铺名称',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(addr == ''){
				uni.showToast({
					title: '请输入店铺地址',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(contacter == ''){
				uni.showToast({
					title: '请输入联系人',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(mobile.length != 11){
				uni.showToast({
					title: '请输入联系人手机',duration: 1500,icon :'none'
				});
				return false
			}
			
			uni.showLoading({
				title: '提交中'
			});
			
			uni.request({
				url: common.apiUrl + 'shop/shenqingViewShopPrice',
				method: 'GET',
				data: {
					shopid:that.shopid,
					userid:userInfo.users_id,
					title:title,
					addr:addr,
					contacter:contacter,
					mobile:mobile,
				},
				success: res => {
					uni.hideLoading();
					var data = res.data
					console.log(data);
					if(data.code == 0){						
						uni.showToast({
							title: '提交成功，等待审核',
							duration: 1500,
							icon:'success'
						});
						setTimeout(function(){
							that.toggleForm();							
						},1500)
					}else{
						uni.showToast({
							title: data.msg,
							duration: 1500,
							icon:'none'
						});
						return false
					}
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		},
			
			
		
		
		toggleForm(){
			var that = this
			that.showForm = !that.showForm;
		},
		
		inputsearch(e){
		  var that = this
		  var keys = e.detail.value;
		  if (keys != that.keys) {
			that.keys = keys
			that.resetAttr();
			that.getSearchGoodsList();
			console.log(that.keys);
		  } else {
		    return false
		  }
		  
		  
		},
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.goodsList = []
		},
		
		//加载更多
		loadmore(){
			var that = this
			that.page = that.page+1;
			that.getSearchGoodsList();
		},
		//搜索店铺列表
		getSearchGoodsList(){
			var that = this
			if(that.keys == ''){
				uni.showToast({
					title:'请输入搜索内容',
					icon:'loading',
					duration:1500,
				})
				return false
			}
			
			uni.request({
				url: common.apiUrl + 'shop/getSearchGoodsList',
				method: 'GET',
				data: {
					keys:that.keys,
					shopid:that.shopid,
					page:that.page+1
				},
				success: res => {
					var data = res.data
					console.log(data)
					var goodsList = data.goods_list
					for(var i=0;i<goodsList.length;i++){
						that.goodsList.push(goodsList[i])
					}						
					that.isall = data.isall
					that.canbuy = data.canbuy
					console.log("goodsList",that.goodsList)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//电话联系
		phoneContact(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.commendShopList
			var mobile = e.currentTarget.dataset.mobile
			uni.makePhoneCall({
				phoneNumber: mobile 
			});
		},
		//一键导航
		goaddr(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.shopList
			var lat = shopList[index].shop_lat
			var lng = shopList[index].shop_lng
			
			console.log(parseFloat(lat));
			console.log(parseFloat(lng));
			uni.openLocation({
				latitude: parseFloat(lat),
				longitude: parseFloat(lng),
				success: function () {
					console.log('success');
				}
			});
		},
		navBack(){
			uni.navigateBack({
				delta:1
			})
		},
		navHome(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		},
		//跳转
		jumpUrl(e){
			var that = this
			if(that.canbuy == 1){
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url:url,
				});
			}
			
		}
	}
}
